function fN(n) {
  if(n == undefined) {
    return 'nn';
  }
  return ('0' + n).slice(-2);
}

function print2D(valuedBoxComp) {
  console.log('********************************');
  for(let i=0; i<4; i++) {
    let str = '** ';
    for(let j=0; j<4; j++) {
      let ind = i.toString() + j;
      str += `${fN(valuedBoxComp[ind])}`;
      if(j<3) {
        str += ', ';
      } else {        
        str += ' **';
      }
    }
    console.log(str);
  }
  console.log('********************************');
}

function redraw(valuedBoxCompArr, valuedBoxComp) {
  for(let i=0; i<4; i++) {
    for(let j=0; j<4; j++) {
      let objKey = i.toString() + j.toString();
      if(valuedBoxComp[objKey] != undefined) {
        let tempComp = valuedBoxCompArr[valuedBoxComp[objKey]];
        tempComp.data.position.top = i*25 + '%';
        tempComp.data.position.left = j*25 + '%';
        
        tempComp.data.position.topi = i;
        tempComp.data.position.lefti = j;
        tempComp.update();
      }
    }
  }
}
function push(valuedBoxCompArr, valuedBoxComp, rC) {

  for(let i=0; i<rC; i++) {
    valuedBoxComp = rotate(valuedBoxComp);
  }

  for(let i=0; i<4; i++) {
    for(let j=1; j<4; j++) {
      let objKey = j.toString() + i.toString();
      if(valuedBoxComp[objKey] != undefined) {
        let prevInd = j;
        let canMove = prevInd-1 > -1 && valuedBoxComp[(prevInd-1) + i.toString()] == undefined;
        let tempCompInd = valuedBoxComp[objKey];
        while(canMove) {
          valuedBoxComp[(prevInd) + i.toString()] = undefined;
          valuedBoxComp[(prevInd-1) + i.toString()] = tempCompInd;
          prevInd--;
          canMove = prevInd-1 > -1 && valuedBoxComp[(prevInd-1) + i.toString()] == undefined;
        }

        if(prevInd > 0) {
          
          let thisComp = valuedBoxCompArr[valuedBoxComp[(prevInd) + i.toString()]];
          let preComp = valuedBoxCompArr[valuedBoxComp[(prevInd-1) + i.toString()]];
          let canMerge = prevInd-1 > -1 && preComp.data.value == thisComp.data.value;

          while(canMerge) {
            valuedBoxComp[(prevInd) + i.toString()] = undefined;

            preComp.data.value += thisComp.data.value;
            thisComp.$root[0].parentElement.removeChild(thisComp.$root[0]);
            prevInd--;
            if(prevInd-1 > -1) {
              thisComp = preComp;
              preComp = valuedBoxCompArr[valuedBoxComp[(prevInd-1) + i.toString()]];
            }
            canMerge = prevInd-1 > -1 && preComp.data.value == thisComp.data.value;
          }
        }
      }      
    }
  }
  for(let i=0; i<4-rC; i++) {
    valuedBoxComp = rotate(valuedBoxComp);
  }
  redraw(valuedBoxCompArr, valuedBoxComp);
  return valuedBoxComp;
}

function rotate(arr) {
  let newArr = {};
  let maxL = 4;
  for(let i=0; i<maxL; i++) {
    for(let j=0; j<maxL; j++) {

      let ind = i.toString() + j.toString();
      if(arr[ind] != undefined) {
        newArr[j + (maxL-1-i).toString()] = arr[ind];
      }
    }
  }
  return newArr;
}
function pushUp(valuedBoxCompArr, valuedBoxComp, cb) {
  var valuedBoxCompNew =push(valuedBoxCompArr, valuedBoxComp, 0);
  cb && cb(valuedBoxCompNew, 3, undefined);
}
function pushDown(valuedBoxCompArr, valuedBoxComp, cb) {
  var valuedBoxCompNew =push(valuedBoxCompArr, valuedBoxComp, 2);
  cb && cb(valuedBoxCompNew, 0, undefined);
}
function pushLeft(valuedBoxCompArr, valuedBoxComp, cb) {
  var valuedBoxCompNew =push(valuedBoxCompArr, valuedBoxComp, 1);
  cb && cb(valuedBoxCompNew, undefined, 3);
}
function pushRight(valuedBoxCompArr, valuedBoxComp, cb) {
  var valuedBoxCompNew = push(valuedBoxCompArr, valuedBoxComp, 3);
  cb && cb(valuedBoxCompNew, undefined, 0);
}

export default {
  pushUp,
  pushDown,
  pushLeft,
  pushRight,
  print2D
};