import getComp from 'setbp/template/component.js';
import mouseActions from 'app/site/helper/push.js';

var emptyboxesrow =[];
var initValues = [2, 4];
var boxComp;
var valuedBoxCompArr = [];
var valuedBoxComp = {};
for(let i=0; i<4; i++) {
  var emptyboxescol =[];
  for(let j=0; j<4; j++) {
    emptyboxescol.push({left: (j*25)+'%', top: (i*25)+'%'});
  }
  emptyboxesrow.push({emptyboxescol});
}

function canGenerate(ci, cj) {
  mouseActions.print2D(valuedBoxComp);
  if(ci == undefined) {
    for(let i=0; i<4; i++) {
      if(valuedBoxComp[i + cj.toString()] == undefined) {
        return true;
      }
    }
  } else {
    for(let j=0; j<4; j++) {
      if(valuedBoxComp[ci.toString() + j] == undefined) {
        return true;
      }
    }
  }
  return false;
}

function addValuedBox(ci, cj) {
  let value = initValues[Math.floor(Math.random()*100)%2];
  let valueGood = false;
  if(!canGenerate(ci, cj)) {
    console.log('row full.');
    return;
  }
  while(!valueGood) {
    let ind = Math.floor(Math.random()*100)%4;
    let isValid = false;
    if(ci == undefined) {
      isValid = valuedBoxComp[ind + cj.toString()] == undefined;
    }
    if(cj == undefined) {
      isValid = valuedBoxComp[ci.toString() + ind] == undefined;
    }
    if(isValid) {
      if(ci == undefined) { ci = ind; }
      if(cj == undefined) { cj = ind; }
      let position = {top: 25*ci+'%', left: 25*cj+'%', topi: ci, lefti: cj};
      var valuedBox = getComp('component/home/valuedbox', {value, position}, {});
      valuedBoxCompArr.push(valuedBox);
      valuedBoxComp[ci.toString() + cj.toString()] = valuedBoxCompArr.length - 1;
      boxComp.$boxCont.append(valuedBox.$root);
      valueGood = true;
    }
  }
}


document.onkeydown = checkKey;
function checkKey(e) {

    e = e || window.event;

    var cb = function(updatedValueBoxComp, ci, cj) {
      valuedBoxComp = (updatedValueBoxComp);
      addValuedBox(ci, cj);
    };
    if (e.keyCode == '38') {
      mouseActions.pushUp(valuedBoxCompArr, valuedBoxComp, cb);
    }
    else if (e.keyCode == '40') {
        // down arrow
        mouseActions.pushDown(valuedBoxCompArr, valuedBoxComp, cb);
    }
    else if (e.keyCode == '37') {
       // left arrow
       mouseActions.pushLeft(valuedBoxCompArr, valuedBoxComp, cb);
    }
    else if (e.keyCode == '39') {
       // right arrow
       mouseActions.pushRight(valuedBoxCompArr, valuedBoxComp, cb);
    }
    e.preventDefault();
}

export default {
  templates: ['site/home'],
  comp: function() {  
    boxComp = getComp('site/home', {emptyboxesrow}, {});
    addValuedBox(3);  
    return boxComp;
  }
};
